import axios from "axios";

let logInInstance = axios.create({
    baseURL         : "https://social-network.samuraijs.com/api/1.0/auth/",
    withCredentials : true,
    headers         : {'API-KEY': '59f426f0-c43b-41f5-beb4-a8fea9d6c501'}, //необходимый идентефикатор от со стороны сервера, привязан к аккаунту
    //timeout         : 3000,
});

export const whoAmI = () => logInInstance("me").then(responese => responese.data);
export const logIn = (email, password, rememberMe = true) => logInInstance.post("login", { email, //"morvarch@protonmail.com",
                                                                  password, //: "Corvus",
                                                                  rememberMe /*: rememberMe,*/}).then(responese => responese.data);
export const logOut = () => logInInstance.delete("login");
