import React from 'react';
import s from './listUsers.module.css'
import Preloader from "../../../common/preloader/preloader";


let ListUsers = (props) => {
    let setClassName = (currentpage, activepage) => {
        if(currentpage === activepage) return 's.active';
    }
    //пусть массив страниц
    let pages = [];
    //делим общее количество пользователей на их количество на странице с округлением вверх
    //циклом создаём ссылки, пробрасывая turnPage, указываем активную
    for(let i = 1; i <= Math.ceil(props.listUsers.totalUserCount/props.listUsers.pageSize); i++) {
        pages.push(<li key={i}><a href="#" onClick={() => props.changeActivePage(i)}  className={setClassName(props.currentPage, i)}>{i}</a></li>);    };
    //выводим массив старниц
    //выводим массив пользователей на странице
    return <div>
        <ul className={s.ul}>{pages}</ul>
        <div>{props.isFetching? <Preloader /> : null}</div>
        <div>{props.showMoreUsers()}</div>
    </div>
}

export default ListUsers;