import React from 'react';
import {connect} from "react-redux";
import {followUserThunkCreator, listUsersThunkCreator, unFollowUserThunkCreator} from "../../../redux/listUsersReducer";
import ListUsers from "./listUsersComponent/listUsers";
import userpicunknown from '../../../assets/userphoto.jpg';
import {withAuthRedirect} from "../../../hoc/withAuthRedirect";
import {compose} from "redux";

class ListUsersContainer extends React.Component{
    //конструктор тут не нужен

    //установка первой страницы в componentDidMount
    componentDidMount() {
        this.props.listUsersThunkCreator(this.props.pageSize, this.props.pageNumber);
    }

    //render
    render() {
        return <ListUsers
            listUsers      = {this.props}
            showMoreUsers = {this._showMoreUsers.bind(this)}
            changeActivePage = {this._turnPage.bind(this)}
        />
    }

    //follow
    _follow = (id) => {
        followUserThunkCreator(id);
    }

    //unfollow
    _unfollow = (id) => {
        unFollowUserThunkCreator(id);
    }

    _turnPage = (page) => {
        this.props.listUsersThunkCreator(this.state.pageSize);
    }

    //_showMoreUsers
    _showMoreUsers = () => {
        return this.props.userlist.map((user) => {
            return <div key={user.id}>
                <div>
                    <img src={user.photos.small ? user.photos.small : userpicunknown} alt={user.name}/>
                </div>
                <div>
                    <span>{user.name}</span>
                    <span>{user.status}</span>
                    <div>{user.followed ?
                        <button onClick={() => this._unfollow(user.id)}>unfollow</button> :
                        <button onClick={() => this._follow(user.id)}>follow</button>
                    }</div>
                </div>
            </div>
        });
    }
};

let mapStateToProps = (state) => state.listUsers;

export const ListUserContainer = compose(
    connect(mapStateToProps, {listUsersThunkCreator, followUserThunkCreator, unFollowUserThunkCreator}),
    withAuthRedirect
)(ListUsersContainer);
