import {profileAPI} from "../api/profileAPI";

const WallPost = 'profileReducer/ADD-WALL-POST';
const SetProfile = 'profileReducer/SET-USER-PROFILE';
const SetStatus = 'profileReducer/SET-STATUS';

let initialState = {
    userInfo: null,
    /*userInfo : {
        userpic     : "./Leif_Eriksson.jpg",
        userName    : "Лейф Эрикссон",
        userAbout   : "Знаменитый мореход."
    },*/
    userWall : [
        /*{
            postText: "Сайт рыбатекст поможет дизайнеру, верстальщику, вебмастеру сгенерировать несколько абзацев более менее осмысленного текста рыбы на русском языке, а начинающему оратору отточить навык публичных выступлений в домашних условиях. При создании генератора мы использовали небезизвестный универсальный код речей. Текст генерируется абзацами случайным образом от двух до десяти предложений в абзаце, что позволяет сделать текст более привлекательным и живым для визуально-слухового восприятия.",
            userName: "Эрик Рыжий",
            userPic: "./Dm6FIeMWsAA7_F8.jpg",
            commentDate: "Пять дней назад",
            id: 1,
        },
        {
            postText: "Сайт рыбатекст поможет дизайнеру, верстальщику, вебмастеру сгенерировать несколько абзацев более менее осмысленного текста рыбы на русском языке, а начинающему оратору отточить навык публичных выступлений в домашних условиях. При создании генератора мы использовали небезизвестный универсальный код речей. Текст генерируется абзацами случайным образом от двух до десяти предложений в абзаце, что позволяет сделать текст более привлекательным и живым для визуально-слухового восприятия.",
            userName: "Эрик Рыжий",
            userPic: "./Dm6FIeMWsAA7_F8.jpg",
            commentDate: "Пять дней назад",
            id: 2,
        }*/
    ],
    userForm : {
        comPic      : "../../../Dm6FIeMWsAA7_F8.jpg",
        comName     : "Эрик Рыжий"
    },
    status: '',
};

let profileReducer = (state = initialState, action) => {
    switch(action.type) {
        case WallPost:
            return {
                ...state,
                currentInput: '',
                userWall: [...state.userWall, {
                    postText: action.text,
                    userName: "Эрик Рыжий",
                    userPic: "./Dm6FIeMWsAA7_F8.jpg",
                    commentDate: new Date().toISOString(),
                    id: 3,
                }],
            };
        case SetProfile:
            return {
                ...state,
                userInfo: action.profile,
            };
        case SetStatus: return {
            ...state,
            status: action.status,
        };
        default:
            return state;
    };
};

export const addWallPost = (text) => ({type: WallPost, text});
export const setProfile = (profile) => ({type: SetProfile, profile: profile});
export const setStatus = (status) => ({type: SetStatus, status: status});

export const getProfileThunkCreator = (profile) => async (dispatch) => {
    let responese = await profileAPI.getUserProfile(profile);
    dispatch(setProfile(responese));
};
export const getStatusThunkCreator = (profile) => async (dispatch) => {
    let responese = await profileAPI.getUserStatus(profile);
    dispatch(setStatus(responese));
}
export const setStatusThunkCreator = (status) => async (dispatch) => {
    let responese = await profileAPI.updateUserStatus(status);
    if(!responese.resultCode) return responese.message;
    dispatch(setStatus(status));
}

export default profileReducer;