import {followUser, getMoreUsers, unFollowUser} from "../api/usersAPI";

const FOLLOW_USER        = 'listUserReducer/FOLLOW_USER';
const UNFOLLOW_USER      = 'listUserReducer/UNFOLLOW_USER';
const FETCH_USERS        = 'listUserReducer/FETCH_USERS';
const IS_FETCHING        = 'listUserReducer/IS_FETCHING';
const USER_QUANTITY      = 'listUserReducer/USER_QUANTITY';
const CURRENT_PAGE       = 'listUserReducer/CURRENT_PAGE';
const FOLLOW_IN_PROGRESS = 'listUserReducer/FOLLOW_IN_PROGRESS';

let initialValue = {
    userlist: [],
    pageSize: 5,
    pageNumber: 1,
    totalUserCount: 1,
    currentPage: 1,
    isFetching: false,
    followingInProgress: [],
}

const listUsersReducer = (state = initialValue, action) => {
    switch(action.type) {
        case FOLLOW_USER: return { ...state, userlist: state.userlist.map( user => {
                if(user.id === action.id) {
                    return {...user, followed: true};
                };
                return {...user};
            })
        };

        case UNFOLLOW_USER: return { ...state, userlist: state.userlist.map( user => {
                if(user.id === action.id) {
                    return {...user, followed: false};
                };
                return {...user};
            })
        };

        case FETCH_USERS: return {...state, userlist: action.users};

        case IS_FETCHING: return {...state, isFetching: action.state};

        case USER_QUANTITY: return {...state, totalUserCount: action.quantity};

        case CURRENT_PAGE: return {...state, currentPage: action.page};

        case FOLLOW_IN_PROGRESS: return {...state,
            followingInProgress: action.state ? [...state.followingInProgress, action.id] : state.followingInProgress.filter(id => !id === action.id)};
        default: {
            return state;
        };
    };
};

export const follow = (id) => ({type: FOLLOW_USER, id});
export const unfollow = (id) => ({type: UNFOLLOW_USER, id});
export const setFetchingState = (state) => ({type: IS_FETCHING, state});
export const setUserQuantity = (quantity) => ({type: USER_QUANTITY, quantity});
export const setCurrentPage = (page) => ({type: CURRENT_PAGE, page});
export const setFollowingState = (id, state) => ({type: FOLLOW_IN_PROGRESS, id, state});
export const fetchUsers = (users) => ({type: FETCH_USERS, users});

export const listUsersThunkCreator = (pageSize = 5, pageNumber = 1) => (dispatch) => {
    //установка isFetching: true
    dispatch(setFetchingState(true));
    //запрос
    getMoreUsers(pageSize, pageNumber).then( responese => {
        if(responese.error) {
            console.log(responese.error);
            return
        };
        //перезаписывание массива findusers
        dispatch(fetchUsers(responese.items));
        //перезаписывание totalUserCount
        dispatch(setUserQuantity(responese.totalCount));
        //перезаписывание currentPage
        dispatch(setCurrentPage(pageNumber));
    });
    //установка isFetching: false
    dispatch(setFetchingState(false));
};

export const followUserThunkCreator = (id) => (dispatch) => {
    //перезаписать followingInProgress с добавлением нужного id
    dispatch(setFollowingState(id, true));
    //послать запрос
    followUser(id).then( responese => {
        if(responese.resultCode) {
            console.log(responese.resultCode);
            return
        };
        //задиспатчить follow
        dispatch(follow(id));
        //перезаписать followingInProgress с удалением нужного id
        dispatch(setFollowingState(id, false));
    });
};

export const unFollowUserThunkCreator = (id) => (dispatch) => {
    //перезаписать followingInProgress с добавлением нужного id
    dispatch(setFollowingState(id, true));
    //послать запрос
    unFollowUser(id).then( responese => {
        if(responese.resultCode) {
            console.log(responese.resultCode);
            return
        };
        //задиспатчить unfollow
        dispatch(unfollow(id));
        //перезаписать followingInProgress с удалением нужного id
        dispatch(setFollowingState(id, false));
    });
};

export default listUsersReducer;