const AddPost = 'dialogReducer/ADD-POST';
const DeletePost = 'dialogReducer/DELETE-POST';

export const addPostActionCreator = (messageText) => ({type:AddPost, messageText});
export const deletePost           = id            => ({type: DeletePost, id});

let initialState = {
    dialogData: [
        {
            id: '1',
            userName: "Эрик Рыжий",
            userPic: "./Dm6FIeMWsAA7_F8.jpg",
            messageText: "Я горд за тебя, сын!"
        },
        {
            id: '2',
            userName: "Лейф Эрикссон",
            userPic: "./Leif_Eriksson.jpg",
            messageText: "Спасибо, отец!"
        },
        {
            id: '3',
            userName: "Эрик Рыжий",
            userPic: "./Dm6FIeMWsAA7_F8.jpg",
            messageText: "К новым свершениям!"
        }
    ],
    collocutors: [
        {
            userID: '1',
            userName: "Эрик Рыжий",
            userPic: "./Dm6FIeMWsAA7_F8.jpg"
        },
        {
            userID: '2',
            userName: "Жуан Ваш Кортириал",
            userPic: "./João_Vaz_Corte-Real.jpg"
        }
    ],
};

let dialogReducer = (state = initialState, action) => {
    switch(action.type) {
        case AddPost:
            return {
                ...state,
                dialogData: [...state.dialogData, {
                    id: '4',
                    userName: "Эрик Рыжий",
                    userPic: "./Dm6FIeMWsAA7_F8.jpg",
                    messageText: action.messageText,
                }],
                currentInput: '',
            };
        case DeletePost: return {...state, dialogData: state.dialogData.filter(post => post.id !== action.id)};
        default:
            return state;
    }
};

export default dialogReducer;