import React from 'react';
import {BrowserRouter, HashRouter} from 'react-router-dom';
import './App.css';
import {Sidebar} from './components/sidebar/Sidebar.js';
import {Content} from './components/content/Content.js';
import {Footer} from './components/footer/Footer.js';
import HeaderContainer from "./components/header/HeaderContainer";
import {compose} from "redux";
import {connect} from "react-redux";
import {appInitializeThunkCreator} from "./redux/appInitialazingReducer";
import Preloader from "./components/common/preloader/preloader";

class App extends React.Component{
    componentDidMount() {
        this.props.appInitializeThunkCreator();
    }

    render() {
        if(!this.props.initialized) {
            return <Preloader/>
        }
        return (
            <div className="App">
                <HashRouter> {/*<BrowserRouter baseurl={url}>*/}
                    <HeaderContainer login={this.props.store.getState().login}/>
                    <div className="wrapper">
                        <Sidebar friendsOnline={this.props.store.getState().sidebar.friendsOnline}/>
                        <Content
                            user={this.props.store.getState().user}
                            dialogs={this.props.store.getState().dialogs}
                            findUsers={this.props.store.getState().findUsers}
                            isAuth={this.props.store.getState().login.isAuth}
                            dispatch={this.props.store.dispatch.bind(this.props.store)}
                            getState={this.props.store.getState.bind(this.props.store)}
                        />
                    </div>
                    <Footer/>
                </HashRouter>
            </div>
        )
    }
}

let mapStateToProps = state => ({
    ...state.appInitialized, me: state.login.id
})

export default compose(connect(mapStateToProps, {appInitializeThunkCreator}))(App);