import {logIn, logOut, whoAmI} from "../api/logInAPI";

const LogIn = 'loginReducer/LOGIN';
const AuthError = 'loginReducer/AUTHERROR';

let initialState = {
    id: null,
    email: null,
    login: null,
    isAuth: false,
    authError: false,
};

let loginReducer = (state = initialState, action) => {
    switch(action.type) {
        case LogIn:
            return{
            ...state,
            authError: false,
            ...action.auth,
        };
        case AuthError: return {...state, authError: action.data}
        default: return state;
    }
};

export const setAuth = (auth) => ({type: LogIn, auth: auth});
export const authError = (data) => ({type: AuthError, data});

export const logInThunkCreator = (email, password, rememberMe) => (dispatch) => {
    return logIn(email, password, rememberMe).then( responese => {
        if(!responese.resultCode) {
            whoAmI().then(responese => {
                if(!responese.resultCode){
                    dispatch(setAuth({...responese.data, isAuth: true, authError: false}))
                }
            });
        } else {
            dispatch(authError(responese.messages));
        }

})}

export const logOutThunkCreator = () => async (dispatch) => {
    let responese = await logOut();
    if(responese.resultCode) return;
    dispatch(setAuth({
        id: null,
        email: null,
        login: null,
        isAuth: false,
        authError: false,
    }));
}

export const whoAmIThunkCreator = () => async dispatch => {
    let responese = await whoAmI();
    if(!responese.resultCode){
        return dispatch(setAuth({...responese.data, isAuth: true, authError: false}))
    };
}

export default loginReducer;