import React from 'react';
import styles from './header.module.css';
import {NavLink} from "react-router-dom";


const Logo = (props) => {
	return(
		<img className="logo" src={props.path} alt={props.alt}/>
	);
}

const AuthThing = (props) => {
	//return props.loginInfo.isAuth ? <span><span>{props.loginInfo.login}</span><button onClick={props.toOgOut}>Выйти</button></span> : <button onClick={props.toLogIn}>Войти</button>;
	return props.loginInfo.isAuth ? <span><span>{props.loginInfo.login}</span><button onClick={props.toOgOut}>Выйти</button></span> : <NavLink to={"/login"}>Войти</NavLink>/*<button>Войти</button>*/
};

const Header = (props) => {
		return(
			<header className={styles.header}>
				<Logo path="./favicon.ico" alt="alt description"/>
				<span>Yep, it's header.</span>
				<AuthThing {...props}/>
			</header>
		);
}
export {Header};