import {profileAPI} from "../api/profileAPI";

const SET_USER_INFO    = 'userPageReducer/SET_USER_INFO';
const NEW_POST         = 'userPageReducer/NEW_POST';
const CURRENT_INPUT    = 'userPageReducer/CURRENT_INPUT';
const SET_STATUS       = 'userPageReducer/SET_STATUS';

let initilaState = {
    userInfo: null,
    userWall: [],
    userForm: {
        comPic      : "./Dm6FIeMWsAA7_F8.jpg",
        comName     : "Эрик Рыжий"
    },
    currentInput: '',
    status: '',
}

let userPageReducer = (state = initilaState, action) => {
    switch (action.type) {
        case SET_USER_INFO: return {...state, userInfo: action.info};
        case NEW_POST: return {
            ...state,
            currentInput: '',
            userWall: [...state.userWall,
                {
                    postText: state.currentInput,
                    userName: "Эрик Рыжий",
                    userPic: "./Dm6FIeMWsAA7_F8.jpg",
                    commentDate: new Date().toISOString(),
                    id: 3,
                }]
        };
        case SET_STATUS: return {...state, status: action.status};
        case CURRENT_INPUT: return {...state, currentInput: action.input}
        default: return state;
    }
}

export const setUserInfo     = (info)   => ({type: SET_USER_INFO, info});
export const addNewPost      = ()       => ({type: NEW_POST});
export const setCurrentInput = (input)  => ({type: CURRENT_INPUT, input});
export const setStatus       = (status) => ({type: SET_STATUS, status});
export const setUserInfoThunkCreator = (userInfo) => (dispatch) => {
    profileAPI.getUserProfile(userInfo).then(
        responese => dispatch(setUserInfo(responese))
    );
};
export const getUserStatusThunkCreator = (id) => (dispatch) => {
    profileAPI.getUserStatus(id).then(
        responese => dispatch(setStatus(responese))
    );
}

export const setUserStatusThunkCreator = (status) =>(dispatch) => {
    profileAPI.updateUserStatus(status).then(
        responese => {
            if(!responese.data.resultCode) console.log('error'+responese.resultCode);
            dispatch(setStatus(status));
        }
    );
};

export default userPageReducer;
