import React  from 'react';
import styles from "./userpost.module.css";

const UserPost = (props) => {
	return(
		<article className={styles.userPost} >
			<div className={styles.userInfo}>
				<img className={styles.userPic} src={props.userPic} alt={props.userName} />
				<p className={styles.userName}>{props.userName}</p>
				<p className={styles.date}>{props.commentDate}</p>
			</div>
			<div className={styles.postContent }>
				<p className={styles.postText}>{props.postText}</p>
				<div className={styles.controls}>
					<button className={styles.button}>Редактировать</button>
					<button className={styles.button}>Удалить</button>
				</div>
			</div>
		</article>
	);
};

export {UserPost};