import React      from 'react';
import {UserPost} from "./userpost/userpost.js";
import {UserForm} from "./form/Form.js";
import styles     from "./UserWall.module.css";

const UserWall = (props) => {

	let userWallPosts = props.userWall.map( post => <UserPost
		postText    = {post.postText}
		userName    = {post.userName}
		userPic     = {post.userPic}
		commentDate = {post.commentDate}
		key         = {post.id}
	/>);

	let addWallPost = (text) => props.addWallPost(text);

	return(
		<div className={styles.userWall}>
			<UserForm
				comPic          = {props.userForm.comPic}
				comName         = {props.userForm.comName}
				addWallPost     = {addWallPost}
			/>
			{userWallPosts}
		</div>
	);
};

export {UserWall};