import {connect} from "react-redux";
import {
    addWallPost,
    getProfileThunkCreator,
    getStatusThunkCreator, setStatusThunkCreator
} from "../../../redux/profileReducer";
import {UserInfo} from "./userinfo/Userinfo";
import {UserWall} from "./userwall/UserWall";
import React from "react";
import {Redirect, withRouter} from "react-router-dom";
import {withAuthRedirect} from "../../../hoc/withAuthRedirect";
import {compose} from "redux";

class Profile extends React.Component {
    componentDidMount() {
        let {match, isAuth, me, getProfileThunkCreator, getStatusThunkCreator} = this.props;
        let profile = match.params.userId;
        if(!profile && isAuth) {
            profile = me;
        }
        getProfileThunkCreator(profile);
        getStatusThunkCreator(profile);
    }

    render() {
        let {me,userInfo,status,setStatusThunkCreator,userWall,userForm,addWallPost, } = this.props;
        if(!me) {
            return <Redirect to={"/login"}/>
        }
        return (
            <div >
                <UserInfo {...userInfo} status={status} updateStatus={setStatusThunkCreator}/>
                <UserWall
                    userWall    = {userWall}
                    userForm    = {userForm}
                    addWallPost = {addWallPost}
                />
            </div>
        );
    }
};

let mapStateToProps = (state) => ({
        userInfo: state.user.userInfo,
        userWall: state.user.userWall,
        userForm: state.user.userForm,
        status: state.user.status,
        me: state.login.id,
        isAuth: state.login.isAuth,
    });

let Profilecontainer = compose(
    withAuthRedirect,
    connect(mapStateToProps, {addWallPost, getProfileThunkCreator, getStatusThunkCreator, setStatusThunkCreator}),
    withRouter
)(Profile);


export default Profilecontainer;