import axios from "axios";

let usersAPI = axios.create({
    baseURL         : "https://social-network.samuraijs.com/api/1.0/",
    withCredentials : true,
    headers         : {'API-KEY': '59f426f0-c43b-41f5-beb4-a8fea9d6c501'}, //необходимый идентефикатор от со стороны сервера, привязан к аккаунту
    //timeout         : 3000,
});

export let getMoreUsers = (pageSize=10, whereto=1) => usersAPI.get("users?count="+pageSize+"&page="+whereto).then(responese => responese.data);
export let followUser   = (id) => usersAPI.post('follow/'+id).then(responese => responese.data); //в post и put есть тело запроса
export let unFollowUser = (id) => usersAPI.delete('follow/'+id).then(responese => responese.data); //в get и delete нет тела запроса
export let getUserProfile = (id) => usersAPI.get('profile/'+id).then(responese => responese.data);

