import {followUser, getMoreUsers, unFollowUser} from "../api/usersAPI";
import {arrayInObjectWorker} from "../utilities/arrayInObjectWorker";

const Follow              = 'findUserReducer/FOLLOW';
const Unfollow            = 'findUserReducer/UNFOLLOW';
const GetMore             = 'findUserReducer/GETMORE';
const TotalUserCount      = 'findUserReducer/TOTALUSERCOUNT';
const CurrentPage         = 'findUserReducer/CURRENTPAGE';
const FetchingState       = 'findUserReducer/SETFETCHINGSTATE';
const FollowingProgress   = 'findUserReducer/FOLLOWINGINPROGRESS';

let initialValue = {
    findusers: [],
    pageSize: 5,
    portionSize: 5,
    pageNumber: 1,
    totalUserCount: 1,
    currentPage: 1,
    isFetching: false,
    followingInProgress: [],
}

let findUsersReducer = (state = initialValue, action) => {
    switch(action.type) {
        case Follow: return {...state, findusers: arrayInObjectWorker(state.findusers, 'id', action.id, {followed: true})};
        case Unfollow: return {...state, findusers: arrayInObjectWorker(state.findusers, 'id', action.id, {followed: false})};
        case GetMore: return {...state, findusers: action.users}; //соединяем старый массив пользователей с новым
        case TotalUserCount: return {
                ...state,
                totalUserCount: action.totalUserCount,
        };
        case CurrentPage: return {
            ...state,
            currentPage: action.currentPage,
        };
        case FetchingState: return{
            ...state, isFetching: action.state,
        };
        case FollowingProgress: return {
            ...state,
            followingInProgress: action.isFetching ? [...state.followingInProgress, action.id] : state.followingInProgress.filter(id => id !== action.id)
        };
        default: {
            return state;
        };
    }
};

export const follow               = (userId) => ({type: Follow, id: userId});
export const unfollow             = (userId) => ({type: Unfollow, id: userId});
export const getMore              = (users)  => ({type: GetMore, users: users});
export const setTotalUserCount    = (totalUserCount) => ({type: TotalUserCount, totalUserCount: totalUserCount});
export const setCurrentPage       = (page) => ({type: CurrentPage, currentPage: page});
export const setFetchingState     = (state) => ({type: FetchingState, state});
export const followingProgress    = (id, isFetching) => ({type: FollowingProgress, id, isFetching});

export const findUserThunkCreator = (pageNumber=1, pageSize=5) => async (dispatch) => {
    dispatch(setFetchingState(true));
    let responese = await getMoreUsers(pageSize, pageNumber);
    dispatch(getMore(responese.items));
    dispatch(setTotalUserCount(responese.totalCount));
    dispatch(setFetchingState(false));
    dispatch(setCurrentPage(pageNumber));
};

let followUnfollowFlow = async (dispatch, id, apiMethod, actionCreator) => {
    dispatch(followingProgress(id, true));
    let responese = await apiMethod(id);
    if(!responese.resultCode) {
        dispatch(actionCreator(id));
        dispatch(followingProgress(id,false));
    }
}

export const followSuccess = (id) => (dispatch) => {
    followUnfollowFlow(dispatch, id, followUser, follow);
};

export const unFollowSuccess = (id) => (dispatch) => {
    followUnfollowFlow(dispatch, id, unFollowUser, unfollow);
};

export default findUsersReducer;