import React     from 'react';
import {NavLink} from 'react-router-dom';
import styles    from './sidebar.module.css';

let Friend = (props) => {
	return <NavLink exact to={`/${props.friend.id}`}>
		<img src={props.friend.userpic} alt={props.friend.userName}/>
		<p>{props.friend.userName}</p>
	</NavLink>
};

let FriendsOnline = (props) => {
	let i = 0;
	let friends = props.friendsOnline.map(friend => <Friend friend={friend} key={i++}/>);

	return <div>{friends}</div>;
};

let NavItem = (props) => {
	//let classes = props.active ? `${styles.navItem} ${styles.active}` : `${styles.navItem}`;
	return(
		<li>
		{/*<li className={classes}>*/}
			<NavLink exact to={props.path} activeClassName={styles.active}>{props.textContent}</NavLink>
		</li>
	);
}

let Nav = (props) => {
	return(
		<ul className="nav">
			<NavItem exact path="/Profile" textContent="Profile" />
			<NavItem exact path="/Messages" textContent="Messages" />
			<NavItem exact path="/Photos" textContent="Photos" />
			<NavItem exact path="/FindUsers" textContent="Find Users" />
			<NavItem exact path="/ListUsers" textContent="List Users"/>
			<NavItem exact path="/Userpage" textContent="Userpage"/>
		</ul>
	);
}

const Sidebar = (props) => {
		return(
			<div className={styles.sidebar}>
				<span>Yep, that's sidebar.</span>
				<Nav />
				<FriendsOnline friendsOnline={props.friendsOnline}/>
			</div>
		);
}
export {Sidebar};