import {whoAmIThunkCreator} from "./loginReducer";

let initialState = {
    initialized: false,
}

const Init_Success = 'appInit/Init_Success';

let appInitializeReducer = ( state = initialState, action) => {
    switch (action.type) {
        case Init_Success: return {
            ...state,
            initialized: true
        };
        default: return state;
    }
}

export const appInitialize = () => ({type: Init_Success});
export const appInitializeThunkCreator = () => (dispatch) => {
    Promise.all([
        dispatch(whoAmIThunkCreator()), //тут может быть много действий, которые нужно выполнить до инициализации
    ]).then(dispatch(appInitialize()));
}

export default appInitializeReducer;