import React from 'react';
import s from "./formControls.module.css";
import {Field} from "react-final-form";

let FormControl = ({input, meta: {touched, error}, children, ...props}) => <div className={s.wrapper+" "+(error && touched ? s.error : "")}>
    {children}
    {error && touched && <div>{error}</div>}
</div>

export let TextArea = (props) => {
    let {input, meta, ...restprops} = props;
    return <FormControl {...props}><textarea {...input} {...restprops} /></FormControl>
}

export let Input = (props) => {
    let {input, meta, ...restprops} = props;
    return <FormControl {...props}><input {...input} {...restprops} /></FormControl>
}
//слегка убирает повторяемость кода, но не до конца

export let createCustomField = (name, component, placeholder, validator, type = null, label = '', props=null) => {
    return <div><label><Field name={name} component={component} placeholder={placeholder} validate={validator} type={type} {...props}/>{label}</label></div>
}