let requiredMessage = "Это поле обязательно.";
let maxLengthMessage = "Не больше 10ти символов.";

let UserForm = (props) => {
	return <Form
				onSubmit={(s, form) => { //три аргумента, для доступа к api формы нужен второй
					props.addWallPost(s.textMessage);
					form.restart(); //зачищаем форму и её состояния
				}}
				validate={() => {}}
				render={({ handleSubmit }) => (
					<form onSubmit={handleSubmit}>
						<div className={styles.userInfo}>
							<img className={styles.comPic} src={props.comPic} alt={props.comName} />
							<p className={styles.comName}>{props.comName}</p>
						</div>

						<div className={styles.form}>
							<Field name={"textMessage"} component={TextArea}
								   						placeholder={"Type your post."}
								   						validate={composeValidator(requiredField(requiredMessage), maxLength(10, maxLengthMessage))}
							/>
						</div>

						<button type="submit">Submit</button>
					</form>
				)}
			/>
}

export {UserForm}