import axios from "axios";

let usersAPI = axios.create({
    baseURL         : "https://social-network.samuraijs.com/api/1.0/",
    withCredentials : true,
    headers         : {'API-KEY': '59f426f0-c43b-41f5-beb4-a8fea9d6c501'}, //необходимый идентефикатор от со стороны сервера, привязан к аккаунту
    //timeout         : 3000,
});

export const profileAPI = {
    getUserProfile  (id) {return usersAPI.get('profile/'+id).then(responese => responese.data)},
    getUserStatus   (id) {return usersAPI.get('profile/status/'+id).then(responese => responese.data)},
    updateUserStatus(text) {return usersAPI.put('profile/status/', {status: text}).then(responese => responese.data)},
}