import React, {Suspense} from 'react';
import {Route} from 'react-router-dom';
import {Photos} from './photos/Photos.js';
import styles from './content.module.css';
//import Messagescontainer from "./messages/messagesContainer";
import Profilecontainer from "./profile/profileContainer";
//import Finduserscontainer from "./findUsers/findUsersContainer";
//import LoginContainer from "./login/loginContainer";
import {ListUserContainer} from "./listUsers/listUsersContainer";
import Preloader from "../common/preloader/preloader";
//import UserPageContainer from "./userpage/userPageContainer";
const Messagescontainer = React.lazy(() => import('./messages/messagesContainer'));
const Finduserscontainer = React.lazy(() => import('./findUsers/findUsersContainer'));
const LoginContainer = React.lazy(() => import('./login/loginContainer'));
const UserPageContainer = React.lazy(() => import('./userpage/userPageContainer'));

let Content = (props) => {

    return (
        <section className={styles.content}>
            {/*<h2>Behold the Content!</h2>*/}
            <Route path='/Profile/:userId?' render={() => <Profilecontainer />}/>
            <Route exact path='/' render={() => <Profilecontainer />}/>
            <Suspense fallback={<Preloader />}>
                <Route path='/Messages' render={() => <Messagescontainer />}/>
                <Route path='/Photos' render={Photos}/>
                <Route path='/FindUsers' render={() => <Finduserscontainer />}/>
                <Route path='/login' render={() => <LoginContainer />}/>
                <Route path='/listusers' render={() => <ListUserContainer />}/>
                <Route path='/Userpage/:userId?' render={() => <UserPageContainer />}/>
            </Suspense>
        </section>
    );
};
export {Content};