import React, {useState, useEffect} from "react";
import s from './userabout.module.css'

const UserstatusWithHooks = props => {

    //все независимо меняющиеся частички состояния компоненты нужно раскидать по разным переменным
    let [editMode, setEditMode] = useState(false);//тут зазаётся только изначальное значение
    let [input, setInput]       = useState(""); //при перерисовке компоненты и, соответсвенно, новом вызове функции оно не учитывается

    useEffect(() => {
        inputStatus(props.status);
    }, [props.status]);//первый аргумент - колбэк
    //второй аргумент - массив зависимостей
    //если переменные в массиве изменится, то выполнится колбек
    //если не ставить зависимостей, то хук будет вызыватсья после каждой перерисовки
    //если передать пустой массив, то хук выполнится лишь однажды, но так лучше не делать


    let inputStatus = text => setInput(text);
    let activateEditeMode = () => setEditMode(true);
    let deActivateEditeMode = () => {
        setEditMode(false)
        props.updateStatus(input);
    };
    //ВАЖНО сразу поместить фокус на нужный элемент, что сработало onBlur
    return <div className={s.userAbout}>
        {editMode ? <div>
            <input type="text" value={input}
                               onChange={e => inputStatus(e.target.value)}
                               onBlur={deActivateEditeMode}
                               autoFocus={true}/>
        </div> :
        <div>
            <p onDoubleClick={activateEditeMode} className={s.p}>{input}</p>
        </div>}
    </div>
}

export default UserstatusWithHooks;