import {applyMiddleware, combineReducers, compose, createStore} from "redux";
import dialogReducer from "./dialogReducer";
import profileReducer from "./profileReducer";
import sidebarReducer from "./sidebarReducer";
import findUsersReducer from "./findUsersReducer";
import loginReducer from "./loginReducer";
import ReduxThunk from "redux-thunk";
import listUsersReducer from "./listUsersReducer";
import userPageReducer from "./userPageReducer";
import appInitializeReducer from "./appInitialazingReducer";

let reducerBundle = combineReducers({
    dialogs      : dialogReducer,
    user         : profileReducer,
    sidebar      : sidebarReducer,
    findusers    : findUsersReducer,
    listUsers    : listUsersReducer,
    login        : loginReducer,
    userPage     : userPageReducer,
    appInitialized: appInitializeReducer

});
//let store = createStore(reducerBundle, applyMiddleware(ReduxThunk));

const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose; //для расширения redux dev tool
const store = createStore(reducerBundle, /* preloadedState, */ composeEnhancers(
    applyMiddleware(ReduxThunk)
));

window.mystore = store;

export default store;