import React  from 'react';
import styles from "./Userinfo.module.css";
import UserstatusWithHooks from "../../../common/userStatusWithHooks/userStatusWithHooks";

const UserInfo = ({userId, contacts, aboutMe, photos, fullName, status, updateStatus, lookingForAJob, lookingForAJobDescription}) => {
	if(!userId) return <div></div>;

	let resources = Object.keys(contacts);

	let links = [];
	resources.map(res => {
		if(!contacts.res) return;
		links.push(<li><a href={contacts.res}>{res}</a></li>);
	});

	return(
		<div className="row">
			<div className={styles.picwrapper}>
				<img className={styles.userpic} src={photos.large} alt={fullName} />
			</div>
			<div className={styles.inner}>
				<h2 className="">{fullName}</h2>
				<p className="">{aboutMe}</p>
				{/*<Userstatus status={props.status} updateStatus={props.updateStatus}/>*/}
				{<UserstatusWithHooks status={status} updateStatus={updateStatus}/>}
				<p>{lookingForAJob ? lookingForAJobDescription : null}</p>
				<ul>{links}</ul>
			</div>
		</div>
	);
};

export {UserInfo};